<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Gestin de Posgrado"
    >
      <v-data-table
        :headers="headers"
        :items="lista_posgrado"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por pgina'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Bsqueda"
              single-line
              hide-details
            />
            <v-spacer />

            <v-dialog
                v-model="dialogReporte"
                max-width="500px"
            >
              <template v-slot:activator="{ on, attrs }" >
                <v-btn
                  color="primary darken-3"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                  
                >
                  Reporte Cantidad Cert. Posgrado
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">Generar Reporte de Cantidad Cert. Posgrado</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <!-- {{reporte}} -->
                      <v-row>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <v-menu
                            ref="menu"
                            v-model="menu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="dateRangeText"
                                label="Seleccione el rango de fechas del reporte"
                                prepend-icon="mdi-calendar"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker
                              v-model="dates"
                              @input="menu = true"
                              range
                              :first-day-of-week="1"
                              locale="es"
                              :rules="fieldRules"
                            >
                            </v-date-picker>
                          </v-menu>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                        >
                          <v-autocomplete
                            v-model="reporte.departamento"
                            :items="lista_distrito"
                            item-text="descripcion"
                            item-value="id"
                            label="Departamento"
                            hint="Seleccione el departamento"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="closeReporte"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="generarReporte"
                    :disabled="!valid"
                  >
                    Generar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog
              v-model="dialog"
              max-width="800px"
            >
              <!-- <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  Nuevo Registro
                </v-btn>
              </template> -->
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitulo }}</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <v-row>
                        <v-col
                        cols="12"
                        sm="6"
                        md="3"
                        >
                          <v-text-field
                            v-model="posgrado.numeroPosgrado"
                            label="N de Posgrado"
                            :counter="50"
                            :rules="fieldRules"
                            :autofocus="true"
                            hint="Introduzca el numero"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="3"
                        >
                          <v-autocomplete
                            v-model="posgrado.tipoPosgrado"
                            :items="lista_tipoposgrado"
                            item-text="tipoPosgrado"
                            item-value="tipoPosgrado"
                            label="Tipo de Posgrado"
                            hint="Seleccione el tipo"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                        >
                          <v-text-field
                            v-model="posgrado.universidad"
                            label="Universidad"
                            :counter="100"
                            :rules="fieldRules"
                            hint="Introduzca la U."
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="4"
                          md="3"
                        >
                          <v-text-field
                            v-model="posgrado.numRegUniversidad"
                            label="N Registro Univ."
                            :counter="100"
                            hint="Introduzca el N"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="4"
                          md="3"
                        >
                          <v-text-field
                            v-model="posgrado.cargaHoraria"
                            label="Carga Horaria"
                            :counter="100"
                            :rules="fieldRules"
                            hint="Introduzca las horas"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                        >
                          <v-text-field
                            v-model="posgrado.titulo"
                            label="Ttulo Obtenido"
                            :counter="100"
                            :rules="fieldRules"
                            hint="Introduzca el ttulo"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-text-field
                            v-model="posgrado.mension"
                            label="Mesin"
                            :counter="100"
                            hint="Introduzca la mensin"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-text-field
                            v-model="posgrado.gradoAcad"
                            label="Grado Acadmico"
                            :counter="100"
                            :rules="fieldRules"
                            hint="Introduzca el grado"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-menu
                            ref="menu1"
                            v-model="menu1"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="posgrado.fechaEmision"
                                label="Fecha Emision"
                                prepend-icon="mdi-calendar"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker
                              v-model="posgrado.fechaEmision"
                              @input="menu1 = false"
                            >
                            </v-date-picker>
                          </v-menu>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="6"
                        >
                          <v-text-field
                            v-model="posgrado.fechaLugarLegal"
                            label="Fecha y Lugar Leg. Titulo"
                            :counter="100"
                            hint="Introduzca"
                            persistent-hint
                          />
                        </v-col>
                        
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDetalle"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h4">Detalle de Certificacin de Posgrado</span>
                </v-card-title>
                <v-card-text>
                  <div class="text-overline mb-1" style="color: black">
                    <p class="text-h6">N Posgrado: {{posgrado.numeroPosgrado}}</p>
                    <p class="text-h6">Fecha Emisin: {{posgrado.fechaEmision}}</p>
                    <p class="text-h6">Carnet Identidad: {{posgrado.Afiliado.ci}}</p>
                    <p class="text-h6">Nombre Completo: {{posgrado.Afiliado.nombreCompleto}}</p>
                    <p class="text-h6">Departamento: {{posgrado.Afiliado.distritoDescripcion}}</p>
                    <p class="text-h6">Profesin: {{posgrado.Afiliado.profesionDescripcion}}</p>
                    <p class="text-h6">Matrcula Colegio: {{posgrado.Afiliado.numeroInscripcion}}</p>
                    <p class="text-h6">Matrcula Profesional: {{posgrado.Afiliado.numeroMinisterio}}</p>
                    <p class="text-h6">Tipo de Posgrado: {{posgrado.tipoPosgrado}}</p>
                    <p class="text-h6">Universidad: {{posgrado.universidad}}</p>
                    <p class="text-h6">N Registro Universidad: {{posgrado.numRegUniversidad}}</p>
                    <p class="text-h6">Carga Horaria: {{posgrado.cargaHoraria}}</p>
                    <p class="text-h6">Titulo Obtenido: {{posgrado.titulo}}</p>
                    <p class="text-h6">Mensin: {{posgrado.mension}}</p>
                    <p class="text-h6">Grado Acadmico: {{posgrado.gradoAcad}}</p>
                    <p class="text-h6">Lugar y Fecha de Legalizacin: {{posgrado.fechaLugarLegal}}</p>
                    
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="closeDetalle"
                  >
                    Cerrar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Est seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ posgrado.Afiliado.ci + ' - ' + posgrado.Afiliado.nombreCompleto }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary darken-2"
            @click="detalle(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-text
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary"
            @click="imprimir(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-printer
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="yellow darken-3"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="red darken-2"
            @click="eliminar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import * as posgradoService from "../services/posgradoService"
import * as distritoService from "../services/distritoService"
import jsPDF from 'jspdf'


  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        dialogDetalle: false,
        dialogReporte: false,
        lista_posgrado: [],
        lista_kardex: [],
        lista_tipoposgrado: [
          'Posgrado', 'Especialidad'
        ],
        lista_distrito: [],
        cargando: true,
        posgrado: {
          Afiliado: {ci: '', nombreCompleto: ''}
        },
        kardex: {
          Persona: {ci:'', paterno:'', materno:'', nombre:''}
        },
        reporte: {
          departamento: 0
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          { text: 'Departamento', value: 'Afiliado.distritoDescripcion' },
          { text: 'N P.', value: 'numeroPosgrado' },
          { text: 'C.I.', value: 'Afiliado.ci' },
          { text: 'Nombre Completo', value: 'Afiliado.nombreCompleto' },
          { text: 'Profesin', value: 'Afiliado.profesionDescripcion' },
          { text: 'Mat. Col.', value: 'Afiliado.numeroInscripcion' },
          { text: 'Mat. Prof.', value: 'Afiliado.numeroMinisterio' },
          { text: 'Titulo', value: 'titulo' },
          // { text: 'Universidad', value: 'universidad' },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultPosgrado: {
          Afiliado: {ci: '', nombreCompleto: ''}
        },
        defaultReporte: {
          departamento: 0
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          // v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu: false,
        menu1: false,
        dates: []
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
      dateRangeText () {
        this.reporte.fechaInicio = this.dates[0]
        this.reporte.fechaFin = this.dates[1]
        return this.dates.join(' ~ ')
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      dialogDetalle (val) {
        val || this.closeDetalle()
      },
      dialogReporte (val) {
        val || this.closeReporte()
      },
    },
    async mounted () {
      // let datos = await posgradoService.listar()
      // console.log(datos);
      // this.lista_posgrado = datos
      // this.cargando = false
      this.obtener()
      this.obtenerDistrito()
    },
    methods: {
      async obtener () {
        const res = await posgradoService.listar()
        // console.log(datos)
        this.lista_posgrado = res.data
        this.cargando = false
      },
      async obtenerDistrito () {
        const res = await distritoService.listar()
        // console.log(res)
        this.lista_distrito = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.posgrado = Object.assign({}, this.defaultPosgrado)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeDetalle () {
        this.dialogDetalle = false
        this.$nextTick(() => {
          this.posgrado = Object.assign({}, this.defaultPosgrado)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeReporte () {
        this.dates = []
        this.dialogReporte = false
        this.$nextTick(() => {
          this.reporte = Object.assign({}, this.defaultReporte)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_posgrado[this.editedIndex]
          res = await posgradoService.modificar(this.posgrado, mar.id)
          Object.assign(this.lista_posgrado[this.editedIndex], this.posgrado)
        } else {
          res = await posgradoService.guardar(this.posgrado)
          // console.log(res);
          this.lista_posgrado.push(this.posgrado)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_posgrado.indexOf(item)
        this.posgrado = Object.assign({}, item)
        this.dialog = true
      },
      detalle (item) {
        this.editedIndex = this.lista_posgrado.indexOf(item)
        this.posgrado = Object.assign({}, item)
        this.dialogDetalle = true
      },
      eliminar (item) {
        this.editedIndex = this.lista_posgrado.indexOf(item)
        this.posgrado = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mar = this.lista_posgrado[this.editedIndex]
          const res = await posgradoService.eliminar(mar.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_posgrado.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          // if (!res.error) {
          //   this.lista_posgrado.splice(this.editedIndex, 1)
          // }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.posgrado = Object.assign({}, this.defaultPosgrado)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      },

      async imprimir(item){
        this.editedIndex = this.lista_posgrado.indexOf(item)
        const pos = this.lista_posgrado[this.editedIndex]
        const res = await posgradoService.imprimirCertificado(pos.id)
        let data = res.data
        this.cargando = false

        let doc = new jsPDF('p','mm','letter')

        let y = 20
        doc.setFont('italic')
        doc.setFontSize(21)
        doc.text(170, y = y + 40, `N ${data.numeroPosgrado}`);
        doc.text(28, y = y + 58, `A:`);
        doc.text(40, y = y, `${(data.nombreCompleto).toUpperCase()}`, {lineHeightFactor: 1,maxWidth:155});
        doc.text(28, y = y + 14, `Ttulo de ${data.tipoPosgrado}:`);
        doc.text(100, y = y, `${data.tituloPosgrado}`, {lineHeightFactor: 1, maxWidth:100});
        doc.text(28, y = y + 22, `Con Carga Horaria:   ${data.cargaHoraria} Horas`);
        doc.text(28, y = y + 14, `Emitido por la:`);
        doc.text(80, y = y, `${data.universidad}`, {lineHeightFactor: 1, maxWidth:120});
        doc.text(28, y = y + 22, `En Fecha:   ${data.fechaLugarLegal}`);
        doc.setFontSize(14)
        doc.text(80, y = y + 65, `Sucre, ${new Date(data.fechaEmision).toLocaleDateString('es', { year:"numeric", month:"long", day:"numeric"})}`);

        window.open(doc.output('bloburl'))
      },


      async generarReporte(){
        const res = await posgradoService.reporteCantidadPosgrado(this.reporte.fechaInicio, this.reporte.fechaFin, this.reporte.departamento)
        let data = res.data
        this.cargando = false

        let doc = new jsPDF()
        var image1 = new Image();
        // var imgData = 'data:image/jpeg;base64,'+ Base64.encode('assets/login.jpg');
        image1.src = 'data:image/jpeg;base64,'; /// URL de la imagen
        doc.addImage(image1, 'JPEG', 20, 10, 15, 20); // Agregar la imagen al PDF (X, Y, Width, Height)

        let y = 20
        doc.setFontSize(10);
        doc.text(62, y, `COLEGIO DE BIOQUMICA Y FARMACIA DE BOLIVIA`);
        doc.setFontSize(14);
        doc.text(20, y = y + 25, `Reporte de Cantidad de Certificaciones de Posgrado y Especialidad`);
        doc.setFontSize(12);
        doc.text(20, y = y + 7, `Periodo del ${this.reporte.fechaInicio} al ${this.reporte.fechaFin}`);
        if (this.reporte.departamento != 0) {
          const dep = await distritoService.listarId(this.reporte.departamento)
          let departamento = dep.data
          doc.text(20, y = y + 7, `Departamento: ${departamento.descripcion}`);
        } else {
          doc.text(20, y = y + 7, `Departamento: Nivel Nacional`);
        }
        doc.setFontSize(11);
        // doc.setFont("italic");
        doc.text(20, y = y + 10, `\u2022 Cantidad de Certificados de Posgrado: ${data.cantidadPosgrado}`);
        doc.text(20, y = y + 7, `\u2022 Cantidad de Certificados de Especialidad: ${data.cantidadEspecialidad}`);

        window.open(doc.output('bloburl'))
        this.closeReporte()
      }
    },    
  }
</script>
